import { h, Component } from 'preact';
import styles from '../../../styles/sass/main.scss';

import { connect } from 'preact-redux';
import { mapDispatchToProps } from '../../store/word';

const mapStoreToProps = (state) => ({
  words: state.word.words,
  wordsDate: state.word.wordsDate,
  isAdmin: state.user.isAdmin,
})

@connect(mapStoreToProps, mapDispatchToProps)
export default class Home extends Component {
  constructor(props){
    super(props);

    this.state = {
      word: {
        word: '',
        mean_vi: '',
        mean_en: '',
        synonym: '',
        created_at: Math.round((new Date()).getTime() / 1000),
        updated_at: Math.round((new Date()).getTime() / 1000),
      },
      valid: {
        word:'',
        mean_vi: '',
      }
    }
  }

  handleChange = (event) => {
    this.setState(function(prevState) {
      return {
        word: {...prevState.word, [event.target.name]: event.target.value }
      }
    })
  }

  handleClick = (event) => {
    if(this.checkValid()) return;

    this.regFirestore();
  }

  regFirestore(){
    let wordData = {...this.state.word}
    wordData.created_at = dateNow()
    wordData.updated_at = dateNow()

    db.collection("words").add(wordData).then((doc) => {
        //TODO
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  regRealtimeDb(){
    const newWordKey = db.ref('words').child('all').push().key;
    let wordData = {...this.state}
    word.created_at = Math.round((new Date()).getTime() / 1000)
    let updates = {};
    if(newWordKey){
      const date = dateNow() || new Date();

      updates['all/' + newWordKey] = wordData;
      updates['date/' + date + '/' + newWordKey] = wordData;

      db.ref('words').update(updates).then(() => {
        this.props.addWord({
          [newWordKey]: wordData
        })
      });
    }
  }

  checkValid(){
    let check = ''

    this.setState({ valid: {} })

    if(!this.state.word.word){
      this.setState({ valid: { ...this.state.valid, word: 'Từ vựng không được để trống'} });
      check = 'error'
    }

    if(!this.state.word.mean_vi){
      this.setState({ valid: { ...this.state.valid, mean_vi: 'Nghĩa tiếng việt không được để trống'} });
      check = 'error'
    }

    return check;
  }

  render(props) {
    return (
      <div>
        <div className="word-wrap">
          <p className="date">&nbsp;</p>

          <ul className="word-list" style="margin-top:10px">
            <li class="m-b-20">
              <input className="input" placeholder="Từ vựng" name="word" type="text" value={this.state.word.word} onChange={this.handleChange} />
              <span className="error">{this.state.valid.word}</span>
            </li>
            <li class="m-b-20">
              <input className="input" placeholder="Nghĩa tiếng việt" name="mean_vi" type="text" value={this.state.word.mean_vi} onChange={this.handleChange} />
              <span className="error">{this.state.valid.mean_vi}</span>
            </li>
            <li class="m-b-20">
              <input className="input" placeholder="Nghĩa tiếng anh" name="mean_en" type="text" value={this.state.word.mean_en} onChange={this.handleChange} />
            </li>
            <li class="m-b-20">
              <input className="input" placeholder="Từ đồng nghĩa" name="synonym" type="text" value={this.state.word.synonym} onChange={this.handleChange} />
            </li>

            <li className="m-b-20">
              <button className="btn btn-save" onClick={this.handleClick} >
                <span> Save </span>
              </button>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}


